.profile {
  display: flex;
  flex-direction: column;
  align-items: center;

  width: 300px;
  margin: 0 auto;

  border: 1px solid #e9eef3;
  border-radius: 5px;
  box-shadow: 0px 0px 6px 5px rgb(161 154 191 / 75%);
}

.description {
  display: flex;
  flex-direction: column;
  align-items: center;

  color: #000000;
}

.avatar {
  width: 200px;
  height: 200px;

  margin-top: 30px;
  margin-bottom: 30px;
}

.name {
  font-weight: 700;
  text-transform: uppercase;

  margin-bottom: 10px;
}

.tag {
  margin-bottom: 10px;
  color: #87888a;
}

.location {
  margin-bottom: 30px;
  color: #87888a;
}

.stats {
  display: flex;
  padding: 0;
  align-items: center;
  justify-content: space-evenly;
  width: 100%;

  border-top: 1px solid #dddddd;
  background-color: #e9eef3;
}

.statsItem {
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 15px;
}

.label {
  font-size: 12px;
  margin-bottom: 5px;
}

.quantity {
  font-weight: 700;
}
